import React, { memo, useEffect, useState } from "react";
import Header from "../../../../partials/Header";
import SideBar from "../../../Partials/SideBar";
import { NavLink } from "react-router-dom";
import "react-perfect-scrollbar/dist/css/styles.css";
import $ from "jquery";
import M from "materialize-css";
import {AddPackageAction} from './../../../../../../entity/Redux/Doctors/Action';
import {useDispatch, useSelector} from 'react-redux';

export default memo(() => {
  const [packDescription, setPackDescription] = useState({
    title : '',
    price : '',
    period : '',
    description : ''
  });
  const [metting, setMetting] = useState({
    name : 'meeting'
  });

  const [chat, setChat] = useState({
    name : "chat",
    free : false
  });

  const [vidoeCall, setVidoeCall] = useState({
    name : "videoCall"
  });

  const dispatch = useDispatch();
  useEffect(() => {
    document.addEventListener("DOMContentLoaded", function () {
      var elems = document.querySelectorAll("select");
      M.FormSelect.init(elems);
    });
    var elems = document.querySelectorAll(".collapsible");
    M.Collapsible.init(elems);
  });

  const handleInputPack = (e) => {
    setPackDescription({
      ...packDescription,
      [e.target.name]: e.target.value,
    });
  };

  const HandleMetting = (e) => {
    setMetting({
      ...metting,
      [e.target.name]: e.target.value,
    });
  };


  const HandleChat = (e) => {
    console.log("targeet chat : " , e.target.value);
    if (e.target.name == "free")
    {
      setChat({
        ...chat ,
        free : e.target.value == "on" ? true : false
      })
    }else{
      setChat({
        ...chat,
        [e.target.name]: e.target.value,
      });
    }
  };


  const HandleVidoeCall = (e) => {
    setVidoeCall({
      ...vidoeCall,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmitForm = (e) => {
    e.preventDefault();
    const finalData = { ...packDescription , items : [] };
    finalData.items.push(metting);
    finalData.items.push(chat);
    finalData.items.push(vidoeCall);

    console.log(metting)
    if (!metting.value || !metting.unit || !chat.unit || !vidoeCall.unit || !vidoeCall.value || !packDescription.title || !packDescription.price || !packDescription.period || !packDescription.description){
      M.toast({html : 'please field all input !'})
    }else{
      // console.log(finalData)
      dispatch(AddPackageAction(finalData));
    }

  };

  return (
    <div>
      <div id="loader-wrapper">
        <div id="loader"></div>
        <div className="loader-section section-left"></div>
        <div className="loader-section section-right"></div>
      </div>
      <Header />
      <div id="main">
        <div className="wrapper">
          <SideBar />
          <div id="back_shado"></div>
          <div className="container">
            <div className="row">
              <div className="col s12">
                <div className="col s12">
                  <div id="breadcrumbs-wrapper">
                    <div className="container">
                      <div className="row">
                        <div className="col s10 m6 l6">
                          <h5 className="breadcrumbs-title">Package Add</h5>
                          <ol className="breadcrumbs">
                            <li>
                              <NavLink to="/doctor">
                                <a>Dashboard</a>
                              </NavLink>
                            </li>
                            <li>
                              <NavLink to="/doctor-list-package">
                                <a>Package Manager</a>
                              </NavLink>
                            </li>
                            <li>
                              <a className="active">Package add</a>
                            </li>
                          </ol>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>

                <form onSubmit={(e) => handleSubmitForm(e)}>
                  <div className="col s12">
                    <div className="main_package_form">
                      <div className="col s12 l6">
                        <div class="col s12">
                          <div
                            className="input-field col s12"
                            style={{ marginTop: 0 }}
                          >
                            <p style={{ color: "#ccc" }}>&nbsp;&nbsp;Title</p>
                            <div id="input_back">
                              <input
                                type="text"
                                name="title"
                                onChange={(e) => handleInputPack(e)}
                                className="validate"
                              />
                            </div>
                          </div>
                        </div>

                        <div class="col s6">
                          <div
                            className="input-field col s12"
                            style={{ marginTop: 0 }}
                          >
                            <p style={{ color: "#ccc" }}>
                              &nbsp;&nbsp;Price
                            </p>
                            <div id="input_back">
                              <input
                                type="text"
                                name="price"
                                onChange={(e) => handleInputPack(e)}
                                className="validate"
                              />
                            </div>
                          </div>
                        </div>

                        <div class="col s6">
                          <div
                            className="input-field col s12"
                            style={{ marginTop: 0 }}
                          >
                            <p style={{ color: "#ccc" }}>
                              &nbsp;&nbsp;period
                            </p>
                            <div id="input_back">
                              <input
                                type="text"
                                name="period"
                                onChange={(e) => handleInputPack(e)}
                                className="validate"
                              />
                            </div>
                          </div>
                        </div>

                        <div
                          className="col s12"
                          style={{ marginBottom: "20px" }}
                        >
                          <div
                            className="input-field col s12"
                            style={{ marginTop: 10 }}
                          >
                            <p style={{ color: "#ccc" }}>
                              &nbsp;&nbsp;Description
                            </p>
                            <div id="input_back">
                              <textarea
                                name="description"
                                onChange={(e) => handleInputPack(e)}
                              />
                            </div>
                          </div>
                        </div>
                      </div>

                      <div className="col s12 l6">
                        <p style={{ color: "#ccc" }}>Package Detial </p>
                        <ul className="collapsible customs-colapse">
                          <li>
                            <div className="collapsible-header">
                              <i className="material-icons">people</i>
                              Meeting
                            </div>
                            <div
                              className="collapsible-body"
                              style={{ background: "#313131" }}
                            >
                              <span>
                                <div className="container">
                                  <div className="row">
                                    <div className="col s12">

                                      <div className="col s6">
                                        <input
                                          placeholder="value"
                                          onChange={(e) => HandleMetting(e)}
                                          type="text"
                                          name="value"
                                          className="validate add_pack_input"
                                        />
                                      </div>
                                      <div className="col s6">
                                        <input
                                          placeholder="unit"
                                          onChange={(e) => HandleMetting(e)}
                                          type="text"
                                          name="unit"
                                          className="validate add_pack_input"
                                        />
                                      </div>

                                    </div>
                                  </div>
                                </div>
                              </span>
                            </div>
                          </li>

                          <li>
                            <div className="collapsible-header">
                              <i className="material-icons">chat</i>
                              Chat
                            </div>
                            <div
                              className="collapsible-body"
                              style={{ background: "#313131" }}
                            >
                              <span>
                                <div className="container">
                                  <div className="row">
                                    <div className="col s12">

                                      <div className="col s4">
                                        <input
                                          placeholder="unit"
                                          type="text"
                                          name="unit"
                                          onChange={(e) => HandleChat(e)}
                                          className="validate add_pack_input"
                                        />
                                      </div>


                                      <div className="col s6">
                                        <div class="switch" id="true-false">
                                          <label style={{ height: 25 }}>
                                            free
                                            <input
                                              onChange={(e) => HandleChat(e)}
                                              type="checkbox"
                                              name="free"
                                            />
                                            <span class="lever"></span>
                                          </label>
                                        </div>
                                      </div>
                                    </div>
                                  </div>
                                </div>
                              </span>
                            </div>
                          </li>

                          <li>
                            <div className="collapsible-header">
                              <i className="material-icons">voice_chat</i>
                              Vidoe Call
                            </div>
                            <div
                              className="collapsible-body"
                              style={{ background: "#313131" }}
                            >
                              <span>
                                <div className="container">
                                  <div className="row">
                                    <div className="col s12">

                                      <div className="col s6">
                                        <input
                                          onChange={(e) => HandleVidoeCall(e)}
                                          placeholder="unit"
                                          type="text"
                                          name="unit"
                                          className="validate add_pack_input"
                                        />
                                      </div>

                                      <div className="col s6">
                                        <input
                                            onChange={(e) => HandleVidoeCall(e)}
                                            placeholder="value"
                                            type="text"
                                            name="value"
                                            className="validate add_pack_input"
                                        />
                                      </div>

                                    </div>
                                  </div>
                                </div>
                              </span>
                            </div>
                          </li>


                        </ul>
                      </div>

                      {/* <div className="col s12 l6">
                      <ul className="collapsible customs-colapse">
                        <li>
                          <div className="collapsible-header">
                            <i className="material-icons">voice_chat</i>
                            Video and Voice call
                          </div>
                          <div className="collapsible-body">
                            <span>
                              <input
                                placeholder="Calling time (minutes)"
                                type="text"
                                className="validate add_pack_input"
                              />
                            </span>
                          </div>
                        </li>
                      </ul>
                    </div>

                    <div className="col s12 l6">
                      <ul className="collapsible customs-colapse">
                        <li>
                          <div className="collapsible-header">
                            <i className="material-icons">games</i>
                            Games
                          </div>
                          <div className="collapsible-body">
                            <span>
                              <select
                                multiple
                                style={{ height: 100, color: "#fff" }}
                                className="browser-default"
                              >
                                <option value="" disabled selected>
                                  Choose type
                                </option>
                                <option value="1">type 1</option>
                                <option value="2">type 2</option>
                                <option value="3">type 3</option>
                              </select>
                              <label>choose game type</label>

                              <div style={{ width: "100%", height: "125px" }}>
                                <div className="input-field col s12 l6">
                                  <input
                                    placeholder="Number of games"
                                    id="first_name"
                                    type="text"
                                    className="validate add_pack_input"
                                  />
                                </div>

                                <div className="input-field col s12 l6">
                                  <input
                                    placeholder="Repeat the game"
                                    id="first_name"
                                    type="text"
                                    className="validate add_pack_input"
                                  />
                                </div>
                              </div>
                            </span>
                          </div>
                        </li>
                      </ul>
                    </div>

                    <div className="col s12 l6">
                      <ul className="collapsible customs-colapse">
                        <li>
                          <div className="collapsible-header">
                            <i className="material-icons">local_hospital</i>
                            medicine
                          </div>
                          <div className="collapsible-body">
                            <span>
                              <select
                                multiple
                                style={{ height: 100, color: "#fff" }}
                                className="browser-default"
                              >
                                <option value="" disabled selected>
                                  Choose type
                                </option>
                                <option value="1">type 1</option>
                                <option value="2">type 2</option>
                                <option value="3">type 3</option>
                              </select>
                              <label>choose medicine type</label>

                              <div style={{ width: "100%", height: "80px" }}>
                                <div className="input-field col s12 l6">
                                  <input
                                    placeholder="Number of medicine"
                                    type="text"
                                    className="validate add_pack_input"
                                  />
                                </div>
                              </div>
                            </span>
                          </div>
                        </li>
                      </ul>
                    </div>

                    <div className="col s12 l6">
                      <ul className="collapsible customs-colapse">
                        <li>
                          <div className="collapsible-header">
                            <i className="material-icons">help_outline</i>
                            Tests
                          </div>
                          <div className="collapsible-body">
                            <span>
                              <select
                                multiple
                                style={{ height: 100, color: "#fff" }}
                                className="browser-default"
                              >
                                <option value="" disabled selected>
                                  Choose type
                                </option>
                                <option value="1">type 1</option>
                                <option value="2">type 2</option>
                                <option value="3">type 3</option>
                              </select>
                              <label>choose test type</label>

                              <div style={{ width: "100%", height: "125px" }}>
                                <div className="input-field col s12 l6">
                                  <input
                                    placeholder="Number of Test"
                                    id="first_name"
                                    type="text"
                                    className="validate add_pack_input"
                                  />
                                </div>

                                <div className="input-field col s12 l6">
                                  <input
                                    placeholder="Repeat the Test"
                                    id="first_name"
                                    type="text"
                                    className="validate add_pack_input"
                                  />
                                </div>
                              </div>
                            </span>
                          </div>
                        </li>
                      </ul>
                    </div> */}

                      <div className="col s12">
                        <div className="col s4" style={{ marginTop: 30 }}>
                          <input
                            type="submit"
                            className="btn btn-success waves-effect green darken-3"
                            style={{ textTransform: "lowercase" }}
                            value={"save package"}
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div id="back_detail">
        <div className="container">
          <div className="row">
            <div className="col s12">
              <div className="col s1 m2 l4"></div>
              <div className="col s10 m8 l4" style={{ marginTop: 30 }}>
                <div className="card grey darken-4">
                  <div className="card-content white-text">
                    <span className="card-title">Delete Package</span>
                    <p>
                      Do you want to delete the <span id="serial_pp"></span>{" "}
                      package?
                    </p>
                  </div>
                  <div className="card-action">
                    <a
                      className="btn btn-secondary waves-effect red"
                      id="deleted"
                      style={{
                        textTransform: "lowercase",
                        marginBottom: 15,
                        float: "left",
                      }}
                    >
                      delete
                    </a>
                    <a
                      className="btn btn-secondary waves-effect blue darken-3"
                      id="cancel"
                      style={{
                        textTransform: "lowercase",
                        marginBottom: 15,
                        float: "right",
                      }}
                    >
                      back
                    </a>
                  </div>
                </div>
              </div>
              <div className="col s1 m2 l4"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
});
